    <div class="text">
      <h4>
        A）How to get Ethereum  hardfork coin-PETH?
        <br/>
The easiest way is to buy a PETH NFT on Opensea, and the ETH address for receiving the forked coins will be recorded. Those purchased before September 1st can receive 10,000 times of Ether in PETH at the time of the fork, and after September 1st, a loss of 90% is 1,000 times of Ether in PETH
      <br/>

      </h4>
     
      <el-button
        type="primary"
        style="margin:10px 0 10px 20px"
        :disabled="disabled"
        @click="getPeth"
        >Claim PETH / 领取PETH</el-button>
      <br/>

<h4>
  B） How to pre-mine PETH from September 1st to September 15th?
  <br/>
Purchased NFT before September 1st or uploaded a PETH address and the snapshot balance on September 1st was greater than 1ether, or entered the whitelist of PETH addresses.
<br/>
</h4>

<h4>
  C) How to get rewards through referral mining?
  <br/>
By recommending qualified pre-mining addresses, you will receive an additional 30% reward for the amount of mining, and an additional 15% reward for re-referrals.<br/>
</h4>

<h4>
  Referrer's PETH address TSxxxx is required, and the community will reward 30% of the pre-mined quantity and 15% to the referrer.
Referee's PETH address TSxxxx , this address is the only address for receiving the forked coins.
  <br/>
</h4>
<h4>
  
The referrer relationship is on the chain, and you can get 30% and 15% of the community extra reward of the following secondary mining.
The conditions for participating in pre-mining are that the 0901 snapshot is greater than 1 ether, or the PETH address that has entered the whitelist.
  <br/>
</h4>

<h4>
  All PETH addresses participating in pre-mining must have a referrer and must have uploaded the PETH address on Ethereum.
  <br/>
</h4>
</div>

<div class="text">
      <h4>
        A）如何领PETH分叉币？
        <br/>
        <h4>
          最简单的办法就是在Opensea上购买一个指定NFT，领取分叉币的ETH地址就被记下来了。9月1日之前购买的就可以在分叉的时候领取一万倍的PETH，9月1日之后损失90%也就是1000倍的PETH。
        </h4>
      </h4>
      <el-button
        type="primary"
        style="margin:10px 0 10px 20px"
        :disabled="disabled"
        @click="getPeth"
        >Claim PETH / 领取PETH</el-button>
      <br/>

<h4>
  B）如何在9月1日至9月15日预挖PETH？
  <br/>
9月1日前购买NFT或者上传过PETH地址并且9月1日快照余额大于1ether，或者进入白名单的PETH地址。

<br/>
</h4>

<h4>
  C）如何通过推荐挖矿获得奖励？
  <br/>
<h4>通过推荐合格的预挖地址，获得挖矿数量的额外30%奖励，再次推荐的额外15%奖励。</h4>
</h4>

<h4>
  推荐人PETH地址 TSxxxx 必填，社区额外奖励预挖数量的30%，15%给推荐人。
被推荐人的PETH地址 TSxxxx ，这个地址是分叉币领取的唯一地址。

  <br/>
</h4>
<h4>
  
  推荐人关系上链，可以获得下面二级挖矿的30%15%的社区额外奖励。
参与预挖的条件是0901快照大于1ether，或者进入白名单的PETH地址。

  <br/>
</h4>

<h4>
  所有参与预挖的PETH地址必须有推荐人，必须在以太坊上传过PETH地址。
  <br/>
</h4>
