    <div class="text">
     <h4>People's Ethereum AKA Peth is an Ethereum hard fork. By signing and uploading the Peth address, Ethereum users can get 10K forked coins(PETH) per ETH before September 1, and 1K after September 1. The fork is expected to happen on September 19.</h4>
        <h4>Bound users will also get the right to pre-mine from September 1st to September 19th, provided that the account balance is greater than 1 ether at the time of the snapshot on September 1st. They can participate in mining with their mobile phones.<br/></h4>
        <h4>Before signing, you need to generate a Peth address first, and you only have one chance to sign. <br/></h4>

       <h4>PETH Introduction <br/></h4>
      <h4>PETH is 3rd generation of Ethereum(ETH3) which solves the performance bottleneck ultimately. PETH allows everyone in the world to participate the network from the mobile devices. PETH is people’s Ethereum. It is for everyone. <br/></h4>

        <h4>Technical Roadmap. <br/></h4>
       <h4>Phase 1: PETH Whitepaper release <br/></h4>
        <h4>Phase 2: Implementation of POCS to empower mobile users and launch of TestNet to get the mining start<br/></h4>
        <h4>Phase 3: MainNet launch for miners<br/></h4>
        <h4>Phase 4: Validators Network Lanuch to allow community to join the network<br/></h4>
        <h4>Phase 5: Ledgers Network Lanuch to enable highly decentralized ledger data storage to achieve the highest security.<br/></h4>
        <h4>Phase 6: PETH becomes a community driven protocol. PETH councile is responsible for the day to day management.<br/></h4>
        <h4>Phase 7: Consensus swarm Launch to solve the performance issue ultimately. More networks adopt PETH technology as their underlying Lay 0 solutions.<br/></h4>
    </div>

    <div class="text">
     <h4>People's Ethereum AKA Peth 是一个以太坊硬分叉。通过签署和上传Peth地址，以太坊用户可以在9月1日之前每ETH获得10K分叉币（PETH），9月1日之后可以获得1K分叉币，预计分叉时间为9月19日。</h4>
        <h4>绑定用户也将获得在 9 月 1 日至 9 月 19 日期间预挖的权利，前提是在 9 月 1 日快照时账户余额大于 1 个以太币。他们可以通过手机参与挖矿。 <br/></h4>
        <h4>绑定前需要先生成一个Peth地址，并且只有一次签名的机会。 <br/></h4>

       <h4> PETH简介 <br/></h4>
      <h4>PETH是第三代以太坊（ETH3），最终解决了性能瓶颈。 PETH 允许世界上的每个人通过移动设备参与网络。 PETH是人们的以太坊。它适合所有人。 <br/></h4>

        <h4>技术路线图 <br/></h4>
       <h4>第一阶段：PETH 白皮书发布 <br/></h4>
        <h4>第二阶段：实施 POCS 赋能移动用户并启动测试网以启动挖矿 <br/></h4>
        <h4>第三阶段：矿工主网启动 <br/></h4>
        <h4> 第四阶段：验证网启动 允许社区加入网络 <br/></h4>
        <h4>第五阶段：账本网 实现高度去中心化的账本数据存储，以实现最高的安全性。 <br/></h4>
        <h4>第六阶段：PETH 成为社区驱动的协议。 PETH 委员会负责日常管理。 <br/></h4>
        <h4> 第七阶段：swarm协议最终解决性能问题。更多的网络采用 PETH 技术作为其底层的 Lay 0 解决方案。 <br/></h4>
    </div>
  </div>

</template>

<script>
import { mapState, mapMutations } from "vuex";
import { formartadd, setLocalStorage, getLocalStorage } from "@/utils/utils";
import $web3js from "../../../src/lib/contract/web3";
import numberUtils from "@/utils/numberUtils";
const Web3 = require("web3");
const iecvote = require("@/lib/contract/contractAbi/iecvote.json");
const donation = require("@/lib/contract/contractAbi/donation.json");
const airdrop = require("@/lib/contract/contractAbi/airdrop.json");

// 捐赠 abi
// 空投 abi
export default {
  data() {
    return {
      recommendAdd: "", // 推荐人地址
      donationAdd: "", // 捐赠地址
      disabled: false,
      formAirdrop: {
        airdropAdd: "",
        airdropNum: "",
      },

      // 社区空投地址数组
      airdropAddArr: [],

      // 没有数量空投的地址, 持币空投地址
      tokenAirDropAddr: "",
      // 持币空投地址
      tokenAirDropAddrArr: [],

      vote: this.$contractAddress.vote,
      iecConst: "",

      donationContractAdd: this.$contractAddress.conadd, // 捐赠合约地址
      donationConst: "", // 捐赠方法主体
      isadid: false,

      airdropConst: "", // 空投方法主体
    };
  },

  computed: {
    ...mapState("login", ["walletaccount"]),
  },

  created() {
    if (getLocalStorage("walletaccount") === null) {
      return;
    }

    this.isDid();
  },

  // watch: {
  //   walletaccount: {
  //     handler(val) {
  //       if (val != "") {
  //         this.disabled = false;
  //       }
  //     },
  //   },
  //   deep: true,
  //   immediate: true,
  // },

  methods: {
    // // 是否是管理员
    // async handlerIsAdmin() {
    //   // 钱包调用固定方法
    //   this.connectMetaMask();
    //   if (getLocalStorage("walletaccount")) {
    //     this.web3 = this.$web3js.getWeb3();
    //   } else {
    //     // 未连接钱包时， 默认连接到 bnb 测试
    //     this.web3 = new Web3(
    //       "https://mainnet.infura.io/v3/a1fb8b522ffc46d38c505a38cb073e73"
    //     );
    //   }
    //   this.myaddress = getLocalStorage("walletaccount")
    //     ? getLocalStorage("walletaccount")
    //     : "0xE91d5D70b07071f85568c15ABfEda2B3466A2705";
    //   this.iecConst = new this.web3.eth.Contract(iecvote.abi, this.vote, {
    //     from: this.myaddress,
    //   });
    //   if (await this.iecConst.methods
    //     .directors(this.myaddress)
    //     .call()){
    //       //  this.disabled = false;
    //     }
    // },

    linkClick() {
      window.open("http://peth.world:6876/classic.html");
    },
    linktg() {
      window.open("https://t.me/petheth3");
    },
    linktwitter() {
      window.open("https://twitter.com/@PETH_ETH3");
    },
    linekEmail() {
      // //   window.open('peth.world@gmail.com')
      window.open("http://peth.world@gmail.com");
    },

    getPeth() {
      window.open(
        "https://opensea.io/assets/ethereum/0x495f947276749ce646f68ac8c248420045cb7b5e/8602975470637798075332173524007230701702998853774010736953099543206491686560"
      );
    },

    // 捐赠
    async handlerSubmit() {
      if (getLocalStorage("walletaccount") === null) {
        this.$message({
          message: "Please link the wallet / 请链接钱包",
          type: "warning",
        });
        return;
      }

      if (this.recommendAdd == "") {
        this.$message({
          message: "Please enter recommender address / 请输入推荐人地址",
          type: "warning",
        });
        return;
      }

      var reg = /^TS-[A-Za-z0-9]/;
      if (!reg.test(this.recommendAdd)) {
        this.$message({
          message: "Check input recommender address format / 请检查推荐人地址格式",
          type: "warning",
        });
        return;
      }


      if (this.donationAdd == "") {
        this.$message({
          message: "Please enter Referee address / 请输入被推荐人地址",
          type: "warning",
        });
        return;
      }

      var reg = /^TS-[A-Za-z0-9]/;
      if (!reg.test(this.donationAdd)) {
        this.$message({
          message: "Check input Referee address format / 请检查被推荐人地址格式",
          type: "warning",
        });
        return;
      }

      if (!await this.recommendAddTrue()) {
        this.$message({
          message: "The recommender does not exist / 推荐人不存在",
          type: "warning",
        });
        return;
      }

      if (await this.addisHave()) {
        this.$message({
          message: "Referral already exists / 被推荐人已存在",
          type: "warning",
        });
        return;
      }

      // if (this.isadid) {
      //   this.$message({
      //     message: "Account submitted / 当前帐户已提交",
      //     type: "warning",
      //   });
      //   return;
      // }

      // if (await this.isDid()) {
      //   this.$message({
      //     message: "当前帐户已提交 / Account submitted",
      //     type: "warning",
      //   });
      //   return;
      // }

      this.connectMetaMask();
      this.web3 = this.$web3js.getWeb3();
      this.myaddress = getLocalStorage("walletaccount");
      this.donationConst = new this.web3.eth.Contract(
        donation.abi,
        this.donationContractAdd,
        {
          from: this.myaddress,
        }
      );
      let vm = this;
      let messageNo;
      // console.log(vm.donationConst);
      vm.donationConst.methods
        .setPeth(vm.recommendAdd, vm.donationAdd)
        .send()
        .on("transactionHash", function (hash) {
          // console.log('hash', hash);
          vm.hash = hash;
          messageNo = vm.$message({
            duration: 0,
            message: "Trading, please do not exit / 交易正在打包，请勿退出",
          });
        })
        .on("receipt", function (receipt) {
          // console.log("交易回执", receipt);
          if (receipt.transactionHash == vm.hash) {
            messageNo.close();
            vm.$message({
              message: "Submitted successfully / 提交成功",
              type: "success",
            });
            vm.donationAdd = "";
          }
        })
        .on("error", function (error, receipt) {
          // console.log("交易失败回执", error, receipt);
          messageNo.close();
          vm.$message({
            message: "Submit failed, try again later / 提交失败,稍后再试",
            type: "warning",
          });
        });
    },

    // 获取 meth
    async isDid() {
      this.connectMetaMask();
      this.web3 = this.$web3js.getWeb3();
      this.myaddress = getLocalStorage("walletaccount");
      this.donationConst = new this.web3.eth.Contract(
        donation.abi,
        this.donationContractAdd,
        {
          from: this.myaddress,
        }
      );

      let isdid = await this.donationConst.methods.IsSave(this.myaddress).call({
        from: this.myaddress,
      });
      // console.log('isdid', isdid);
      this.isadid = isdid;
      return isdid;
    },

    async addisHave() {
      this.connectMetaMask();
      this.web3 = this.$web3js.getWeb3();
      this.myaddress = getLocalStorage("walletaccount");
      this.donationConst = new this.web3.eth.Contract(
        donation.abi,
        this.donationContractAdd,
        {
          from: this.myaddress,
        }
      );

      let addishave = await this.donationConst.methods
        .PethIsSave(this.donationAdd)
        .call({
          from: this.myaddress,
        });
      // console.log('addishave', addishave);
      return addishave;
    },

    // pethlssave 推荐人
    async recommendAddTrue() {
      this.connectMetaMask();
      this.web3 = this.$web3js.getWeb3();
      this.myaddress = getLocalStorage("walletaccount");
      this.donationConst = new this.web3.eth.Contract(
        donation.abi,
        this.donationContractAdd,
        {
          from: this.myaddress,
        }
      );

      let addishave = await this.donationConst.methods
        .PethIsSave(this.recommendAdd)
        .call({
          from: this.myaddress,
        });
      // console.log('addishave', addishave);
      return addishave;
    },

    connectMetaMask() {
      $web3js.connectMetaMask().then((res) => {
        if (!getLocalStorage("walletaccount")) {
          // this.loadingData();
        }
      });
    },
  },
};
</script>

