<template>
  <div class="airdrop-wrap">
    <div class="introduce-wrap">
      <div style="margin-left: 30px" v-if="!isairdropnoexpire">
        <h2>Deadline of the period：</h2>
        <h3 style="margin:5px 15px 0;">
          {{ formatDateTime(airdropdetailendtime * 1000) || "--" }}
        </h3>
        <h2 style="margin:8px 0">Airdrop number：</h2>
        <h3 style="margin:5px 15px 0;">
          {{ airdropCoinNumber || 0 }}
        </h3>
        <h2 style="margin:8px 0">Total number of airdrops：</h2>
        <h3 style="margin:5px 15px 0;">
          {{ airdropCoinTatol || 0 }}
        </h3>
        <h2 style="margin:8px 0">Airdropped quantity：</h2>
        <h3 style="margin:5px 15px 0;">
          {{ hasairdropNumber || 0 }}
        </h3>
      </div>

      <div style="margin: 20px 0 50px">
        <el-button
          type="primary"
          style="margin-left: 30px"
          :disabled="disabled"
          @click="receiveAirdrop"
          >Receive Airdrop</el-button
        >
      </div>

      <!-- 只有是管理员的时候才显示 -->
      <div style="margin-bottom: 70px" v-if="isadmin && isairdropnoexpire">
        <el-form ref="form" label-width="140px">
          <el-form-item label="Expire Date:">
            <el-input
              v-model="expireDate"
              placeholder="Please input the Expire Date"
              maxlength="“45"
            ></el-input>
          </el-form-item>

          <el-form-item label="Number of airdrops:">
            <el-input
              v-model="receiveNum"
              placeholder="Please input the Number of airdrops"
              maxlength="“45"
            ></el-input>
          </el-form-item>

          <el-form-item label="Airdrop Total:">
            <el-input
              v-model="airdropTotal"
              placeholder="Please input the Airdrop Total"
              maxlength="“45"
            ></el-input>
          </el-form-item>
        </el-form>
        <el-button
          v-if="isAuthedSuccess"
          type="primary"
          style="margin-left: 30px"
          :disabled="disabled"
          @click="setExpiredTime"
          >Set Config</el-button
        >
        <el-button
          v-else
          type="primary"
          style="margin-left: 30px"
          :disabled="disabled"
          @click="handlerToAuth"
          >Authorize</el-button
        >
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import {
  formartadd,
  formatDateTime,
  setLocalStorage,
  getLocalStorage,
} from "@/utils/utils";
import $web3js from "../../../lib/contract/web3";
import numberUtils from "@/utils/numberUtils";
const Web3 = require("web3");
const iecToken = require("@/lib/contract/contractAbi/iectoken.json");
const onlyairdrop = require("@/lib/contract/contractAbi/onlyairdrop.json");

// 捐赠 abi
// 空投 abi
export default {
  data() {
    return {
      disabled: true,
      isadmin: false,
      // 领取空投参数 id
      receiveId: "",
      iectokenAuthed: "", // 授权方法主体
      isAuthedSuccess: false,

      iecTokenContract: this.$contractAddress.iecToken,
      iecConst: "",
      expireDate: "", // 过期时间
      airdropTotal: "", // 空投总量
      receiveNum: "", // 领取数量
      onlyairdropContractAdd: this.$contractAddress.onlyairdropContractAdd, // 空投合约地址
      onlyairdropConst: "", // 空投方法主体

      isairdropnoexpire: false, // 本期空投是否过期
      // 空投详情
      airdropdetailendtime: "", //截止时间
      airdropCoinNumber: "", // 每笔空投lec数量
      airdropCoinTatol: "", //本期空投lec总数量
      hasairdropNumber: "", // 已经空投lec数量
    };
  },

  computed: {
    ...mapState("login", ["walletaccount"]),
  },

  activated() {
    this.disabled = getLocalStorage("walletaccount") ? false : true;
    if (getLocalStorage("walletaccount")) {
      // 是否是管理员
      this.handlerIsAdmin();
      this.getreceiveId();
      // 是否授权
      this.isAuthed();
    }
  },

  methods: {
    formatDateTime,
    // 是否授权
    isAuthed() {
      // 获得方法主体
      this.connectMetaMask();
      if (getLocalStorage("walletaccount")) {
        this.web3 = this.$web3js.getWeb3();
      } else {
        // 未连接钱包时， 默认连接到 bnb 测试
        this.web3 = new Web3(
          "https://mainnet.infura.io/v3/a1fb8b522ffc46d38c505a38cb073e73"
        );
      }
      this.myaddress = getLocalStorage("walletaccount")
        ? getLocalStorage("walletaccount")
        : "0xE91d5D70b07071f85568c15ABfEda2B3466A2705";
      this.iectokenAuthed = new this.web3.eth.Contract(
        iecToken.abi,
        this.iecTokenContract,
        {
          from: this.myaddress,
        }
      );
      let vm = this;
      vm.iectokenAuthed.methods
        .allowance(vm.myaddress, vm.onlyairdropContractAdd)
        .call({ from: vm.myaddress })
        .then((res) => {
          // console.log("shouquan", res);
          if (res > 0) {
            this.isAuthedSuccess = true;
            return;
          }
        });
    },
    // 是否是管理员
    async handlerIsAdmin() {
      // 钱包调用固定方法
      this.connectMetaMask();
      if (getLocalStorage("walletaccount")) {
        this.web3 = this.$web3js.getWeb3();
      } else {
        // 未连接钱包时， 默认连接到 bnb 测试
        this.web3 = new Web3(
          "https://mainnet.infura.io/v3/a1fb8b522ffc46d38c505a38cb073e73"
        );
      }
      this.myaddress = getLocalStorage("walletaccount")
        ? getLocalStorage("walletaccount")
        : "0xE91d5D70b07071f85568c15ABfEda2B3466A2705";
      this.iecConst = new this.web3.eth.Contract(
        onlyairdrop.abi,
        this.onlyairdropContractAdd,
        {
          from: this.myaddress,
        }
      );
      if (await this.iecConst.methods.admin(this.myaddress).call()) {
        this.isadmin = true;
      }
    },
    // 查领取空头参数 id  receiveId
    async getreceiveId() {
      // 钱包调用固定方法
      this.connectMetaMask();
      if (getLocalStorage("walletaccount")) {
        this.web3 = this.$web3js.getWeb3();
      } else {
        // 未连接钱包时， 默认连接到 bnb 测试
        this.web3 = new Web3(
          "https://mainnet.infura.io/v3/a1fb8b522ffc46d38c505a38cb073e73"
        );
      }
      this.myaddress = getLocalStorage("walletaccount")
        ? getLocalStorage("walletaccount")
        : "0xE91d5D70b07071f85568c15ABfEda2B3466A2705";
      this.iecConst = new this.web3.eth.Contract(
        onlyairdrop.abi,
        this.onlyairdropContractAdd,
        {
          from: this.myaddress,
        }
      );
      const getedid = await this.iecConst.methods.id().call();
      // console.log("getedid", getedid);
      this.receiveId = getedid;
      this.getairdropdetail(this.receiveId);
    },
    // 获取每期空投详情
    async getairdropdetail(id) {
      // 钱包调用固定方法
      this.connectMetaMask();
      if (getLocalStorage("walletaccount")) {
        this.web3 = this.$web3js.getWeb3();
      } else {
        // 未连接钱包时， 默认连接到 bnb 测试
        this.web3 = new Web3(
          "https://mainnet.infura.io/v3/a1fb8b522ffc46d38c505a38cb073e73"
        );
      }
      this.myaddress = getLocalStorage("walletaccount")
        ? getLocalStorage("walletaccount")
        : "0xE91d5D70b07071f85568c15ABfEda2B3466A2705";
      this.iecConst = new this.web3.eth.Contract(
        onlyairdrop.abi,
        this.onlyairdropContractAdd,
        {
          from: this.myaddress,
        }
      );
      const airdropdetail = await this.iecConst.methods.airdropMap(id).call();
      // console.log("airdropdetail", airdropdetail);
      this.airdropdetailendtime = airdropdetail.endTime;

      // 到期了
      let currentTime = new Date().getTime();
      if (currentTime > this.airdropdetailendtime * 1000) {
        this.isairdropnoexpire = true;
      }

      this.airdropCoinNumber = await numberUtils.movePointLeft(
        airdropdetail.airdropCoinNumber,
        18
      );
      this.airdropCoinTatol = await numberUtils.movePointLeft(
        airdropdetail.airdropCoinTatol,
        18
      );
      this.hasairdropNumber = await numberUtils.movePointLeft(
        airdropdetail.hasairdropNumber,
        18
      );
    },
    // 领取空投
    async receiveAirdrop() {
      this.connectMetaMask();
      this.web3 = this.$web3js.getWeb3();
      this.myaddress = getLocalStorage("walletaccount");
      this.onlyairdropConst = new this.web3.eth.Contract(
        onlyairdrop.abi,
        this.onlyairdropContractAdd,
        {
          from: this.myaddress,
        }
      );
      let vm = this;
      let messageNo;
      vm.onlyairdropConst.methods
        .receiveAirdrop(vm.receiveId)
        .send()
        .on("transactionHash", function(hash) {
          vm.hash = hash;
          messageNo = vm.$toast({
            duration: 0,
            message: "In transaction, please wait patiently",
          });
        })
        .on("receipt", function(receipt) {
          // console.log("交易回执", receipt);
          if (receipt.transactionHash == vm.hash) {
            messageNo.close();
            vm.$toast({
              message: "Receive Donation is successful",
            });
            vm.donationAdd = "";
            vm.getreceiveId();
            vm.$store.dispatch("ActionRouterKey");
          }
        })
        .on("error", function(error, receipt) {
          // console.log("交易失败回执", error, receipt);
          messageNo.close();
          vm.$toast({
            message: "Receive Donation failed, try later",
          });
        });
    },

    // 管理员设置过期时间
    setExpiredTime() {
      this.isAuthed();
      if (this.isAuthedSuccess) {
        if (this.expireDate == "") {
          this.$toast({
            message: "expire Date cannot be empty",
          });
          return;
        }

        if (this.receiveNum == "") {
          this.$toast({
            message: "Quantity received cannot be empty",
          });
          return;
        }

        if (this.airdropTotal == "") {
          this.$toast({
            message: "Airdrop total cannot be empty",
          });
          return;
        }
        this.setconfigInfo();
      } else {
        this.handlerToAuth();
      }
    },

    // 调用合约穿参数
    async setconfigInfo() {
      this.connectMetaMask();
      this.web3 = this.$web3js.getWeb3();
      this.myaddress = getLocalStorage("walletaccount");
      this.onlyairdropConst = new this.web3.eth.Contract(
        onlyairdrop.abi,
        this.onlyairdropContractAdd,
        {
          from: this.myaddress,
        }
      );
      let vm = this;
      let messageNo;
      let endtime = vm.expireDate * 24 * 60 * 60;
      // console.log("endtime", endtime);
      let endTime_ = endtime;
      let airdropCoinNumber_ = await numberUtils.movePointRight(
        vm.receiveNum,
        18
      );
      let airdropCoinTatol_ = await numberUtils.movePointRight(
        vm.airdropTotal,
        18
      );
      // console.log(vm.receiveNum, vm.airdropTotal);
      vm.onlyairdropConst.methods
        .newAirDrop(endTime_, airdropCoinNumber_, airdropCoinTatol_)
        .send()
        .on("transactionHash", function(hash) {
          vm.hash = hash;
          messageNo = vm.$toast({
            duration: 0,
            message: "In transaction, please wait patiently",
          });
        })
        .on("receipt", function(receipt) {
          // console.log("交易回执", receipt);
          if (receipt.transactionHash == vm.hash) {
            messageNo.close();
            vm.$toast({
              message: "Set successfully",
            });
          }
          vm.getreceiveId();
          vm.$store.dispatch("ActionRouterKey");
          vm.expireDate = ""; // 过期时间
          vm.airdropTotal = ""; // 空投总量
          vm.receiveNum = ""; // 领取数量
        })
        .on("error", function(error, receipt) {
          // console.log("交易失败回执", error, receipt);
          messageNo.close();
          vm.$toast({
            message: "Set failed, try later",
          });
        });
    },

    // 授权
    async handlerToAuth() {
      // 获得方法主体
      this.connectMetaMask();
      this.web3 = this.$web3js.getWeb3();
      this.myaddress = getLocalStorage("walletaccount");
      this.iectokenAuthed = new this.web3.eth.Contract(
        iecToken.abi,
        this.iecTokenContract,
        {
          from: this.myaddress,
        }
      );
      //转化价格
      this.amount = await numberUtils.movePointRight(99999999, 18);
      let vm = this;
      let messageNo;
      vm.iectokenAuthed.methods
        .approve(vm.onlyairdropContractAdd, vm.amount)
        .send({ from: vm.myaddress })
        .on("transactionHash", function(hash) {
          messageNo = vm.$toast({
            duration: 0,
            message: "In transaction, please wait patiently",
          });
          // console.log("交易广播哈希", hash);
          vm.hash = hash;
        })
        .on("receipt", function(receipt) {
          // console.log("交易回执", receipt);/
          if (receipt.transactionHash == vm.hash) {
            messageNo.close();
            vm.$toast({
              message:
                "Authorization is successful, fill in the configuration information and submit",
            });
            vm.isAuthedSuccess = true;
          }
        })
        .on("error", function(error, receipt) {
          // console.log("交易失败回执", error, receipt);
          messageNo.close();
          vm.$message({
            message: "Failed authorization",
            type: "success",
          });
        });
    },
    connectMetaMask() {
      $web3js.connectMetaMask().then((res) => {
        if (!getLocalStorage("walletaccount")) {
          // this.loadingData();
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
.airdrop-wrap {
  padding: 10px 0 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  // align-items: center;
  // width: 90%;
  // margin: 0 auto;
  .introduce-wrap {
    display: flex;
    flex-direction: column;
    // width: 60%;
    height: 150px;
    border-radius: 12px;
    padding: 20px 15px;
    font-size: 16px;
    > div:nth-child(1) {
      justify-content: space-between;
    }
  }
}
</style>
