<template>
  <div class="post-motion">
    <div class="content">
      <Field
        v-model="recommendAdd"
        autosize
        type="textarea"
        label="Referrer's Account / 推荐人地址："
        placeholder="TS-G2U3-GSPS-56C2-HHBNX"
      >
    </Field>

      <Field
        v-model="donationAdd"
        autosize
        type="textarea"
        label="Referal Account / 被推荐人地址："
        placeholder="TS-62L8-3W9P-BFL7-EFRHV"
      >
      </Field>
      <Button  
        style="margin: 10px 0;"
        type="info" 
        :disabled="disabled"
        @click="handlerSubmit">
        Submit / 提 交 
      </Button>
    </div>
  </div>
</template>

<script>
import { Field, Button, Tab, Tabs } from "vant";
import { mapState, mapMutations } from "vuex";
import { formartadd, setLocalStorage, getLocalStorage } from "@/utils/utils";
import $web3js from "../../../lib/contract/web3";
import numberUtils from "@/utils/numberUtils";
const Web3 = require("web3");
const iecvote = require("@/lib/contract/contractAbi/iecvote.json");
const donation = require("@/lib/contract/contractAbi/donation.json");
const airdrop = require("@/lib/contract/contractAbi/airdrop.json");
export default {
  data() {
    return {
      recommendAdd: '', // 推荐人地址
      donationAdd: "", // 捐赠地址
      disabled: false,

      vote: this.$contractAddress.vote,
      iecConst: "",

      donationContractAdd: this.$contractAddress.conadd, // 捐赠合约地址
      donationConst: "", // 捐赠方法主体
      isadid: false,
    };
  },

  components: {
    Field,
    Button,
    Tab,
    Tabs,
  },

  created() {
    if (getLocalStorage("walletaccount") === null) {
      return;
    }
    // this.isDid()
  },

  methods: {
    // 捐赠
    async handlerSubmit() {
      // console.log('lll', this.myaddress);
      if (getLocalStorage("walletaccount") === null) {
        this.$toast({ message: "Link the wallet / 请链接钱包" });
        return;
      }

      if (this.recommendAdd === "") {
        this.$toast({ message: "enter recommender address / 请输入推荐人地址" });
        return;
      }

      var reg = /^TS-[A-Za-z0-9]/;

      if (!reg.test(this.recommendAdd)) {
        this.$toast({
          message: "Check input recommender address format / 请检查推荐人地址格式"
        });
        return;
      }


      if (this.donationAdd === "") {
        this.$toast({ message: "enter Referee address / 请输入被推荐人地址" });
        return;
      }

      var reg = /^TS-[A-Za-z0-9]/;

      if (!reg.test(this.donationAdd)) {
        this.$toast({
          message: "Check input Referee address format / 请检查被推荐人地址格式"
        });
        return;
      }

      if (!await this.recommendAddTrue()) {
        this.$$toast({
          message: "The recommender does not exist / 推荐人不存在"
        });
        return;
      }

      if (await this.addisHave()) {
        this.$toast({
          message: "Referral already exists / 被推荐人已存在",
        });
        return;
      }

      // if(this.isadid) {
      //   this.$toast({
      //     message: "Account submitted / 当前帐户已提交",
      //   });
      //   return;
      // }

      // if (await this.isDid()) {
      //   this.$toast({
      //     message: "Account submitted / 当前帐户已提交",
      //   });
      //   return;
      // }


      this.connectMetaMask();
      this.web3 = this.$web3js.getWeb3();
      this.myaddress = getLocalStorage("walletaccount");
      this.donationConst = new this.web3.eth.Contract(
        donation.abi,
        this.donationContractAdd,
        {
          from: this.myaddress,
        }
      );
      let vm = this;
      let messageNo;
      // console.log(vm.donationConst);
      vm.donationConst.methods
        .setPeth(vm.recommendAdd, vm.donationAdd)
        .send()
        .on("transactionHash", function (hash) {
          vm.hash = hash;
          messageNo = vm.$toast({
            duration: 0,
            message: "Trading, please do not exit / 交易正在打包，请勿退出",
          });
        })
        .on("receipt", function (receipt) {
          // console.log("交易回执", receipt);
          if (receipt.transactionHash == vm.hash) {
            messageNo.close();
            vm.$toast({
              message: "Submitted successfully / 提交成功",
            });
            vm.donationAdd = "";
          }
        })
        .on("error", function (error, receipt) {
          // console.log("交易失败回执", error, receipt);
          messageNo.close();
          vm.$toast({
            message: "Submit failed, try again later / 提交失败,稍后再试",
          });
        });
    },

    async isDid() {
      this.connectMetaMask();
      this.web3 = this.$web3js.getWeb3();
      this.myaddress = getLocalStorage("walletaccount");
      this.donationConst = new this.web3.eth.Contract(
        donation.abi,
        this.donationContractAdd,
        {
          from: this.myaddress,
        }
      );

      let isdid = await this.donationConst.methods
        .IsSave(this.myaddress)
        .call({
          from: this.myaddress,
        });
        // console.log('isdid', isdid);
        this.isadid = isdid;
        return isdid;
    },

    async addisHave() {
      this.connectMetaMask();
      this.web3 = this.$web3js.getWeb3();
      this.myaddress = getLocalStorage("walletaccount");
      this.donationConst = new this.web3.eth.Contract(
        donation.abi,
        this.donationContractAdd,
        {
          from: this.myaddress,
        }
      );

      let addishave = await this.donationConst.methods
        .PethIsSave(this.donationAdd)
        .call({
          from: this.myaddress,
        });
        // console.log('isdid', isdid);
        return addishave;
    },

        // pethlssave 推荐人
    async recommendAddTrue() {
      this.connectMetaMask();
      this.web3 = this.$web3js.getWeb3();
      this.myaddress = getLocalStorage("walletaccount");
      this.donationConst = new this.web3.eth.Contract(
        donation.abi,
        this.donationContractAdd,
        {
          from: this.myaddress,
        }
      );

      let addishave = await this.donationConst.methods
        .PethIsSave(this.recommendAdd)
        .call({
          from: this.myaddress,
        });
      // console.log('addishave', addishave);
      return addishave;
    },


    connectMetaMask() {
      $web3js.connectMetaMask().then((res) => {
        if (!getLocalStorage("walletaccount")) {
          // this.loadingData();
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
.post-motion {
  > h2 {
    font-size: 16px;
    padding: 15px;
  }
  .content {
    padding: 0 15px;
    margin-top: 25px;
    // text-align: center;
    .optionBox {
      display: flex;
      align-items: center;
      .size {
        display: flex;
        align-items: center;
        width: 32px;
        height: 32px;
      }
    }
    .btnbox {
      display: flex;
      margin-top: 45px;
      justify-content: center;
      > button:nth-child(1) {
        margin-right: 30px;
      }
    }
  }
}

